<template>
  <div class="classes">
    <div
      class="classesItem"
      v-for="(item, index) in categoryList.top_tab"
      :key="index"
      @click="updateList(item.category_id)"
    >
      <img :src="item.img_url" alt="" />
      <p>{{ item.name }}</p>
      <div
        class="select"
        :class="{ active: activeIndex == item.category_id }"
      ></div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      activeIndex: '',
    };
  },
  computed: { ...mapState(["categoryList"]) },
  methods: {
    ...mapActions(["getCategoryList"]),
    updateList(id) {
      this.activeIndex = id;
      this.getCategoryList(id);
    },
  },
  activated(){
    this.activeIndex = this.$route.query.id
  }
};
</script>

<style lang="less" scoped>
.classes {
  overflow-y: hidden;
  overflow-x: scroll;
  background: #f4f4f4;
  font-size: 0.24rem;
  white-space: nowrap;
  z-index: 2;
  padding: 0.25rem 0;
  text-align: left;
  width: 100%;
  transition: transform 0.3s;

  .classesItem {
    display: inline-block;
    background: #fff;
    padding-bottom: 0.1rem;
    width: 1.3rem;
    margin-left: 0.12rem;
    overflow-x: hidden;
    text-align: center;
    border-radius: 0.04rem;
    padding: 8px;
    position: relative;

    .select {
      width: 0.28rem;
      height: 0.05rem;
      background: #ff5934;
      position: absolute;
      bottom: 0;
      left: 40%;
      display: none;
      &.active {
        display: block;
      }
    }

    img {
      display: block;
      width: 1.28rem;
      height: 1.28rem;
      margin: 0.06rem auto;
    }
    p {
      margin: 0;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
}
</style>